.main{
 padding-top: 10vh;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.main_skaner{
  padding-top: 15vh;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-image: url("flamingo.jpg");
  background-size:     cover;                      /* <------ */
  background-repeat:   no-repeat;
  background-position: center center;
}
.header{
  position: fixed;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;
}
.result{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: grey;
  height: 10vh;
}
.resultMore{
  width: 100%;
  text-align: center;
position: relative;
color: white;
font-size:20px;
}
.resultMoreText{
margin: 0 0 !important;
}
.motto{
  display: flex;
  justify-content:center;
  /* align-items: center; */
  color: white;
  transform: rotate(180deg);
  background-color: grey;
  width: 100%;
  height: 50%;
  box-shadow: inset 0 0 10px #000000;
}
.madeBy{
  display: flex;
  justify-content:center;
  /* align-items: center; */
  color: white;
  background-color: lightblack;
  width: 100%;
  height: 50%;
}
.madeByLogo{
  flex: 1;
  text-align: center;
}
.madeByTeam{
  flex: 2;
  text-align: center;
}


.footer{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  background-color: black;
}
.showMore{
  position: absolute;
  right: 3vh;
  background-color:white;
  border: none;
  color: white;
border-radius: 100%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  width: 6vw;
  height: 3vh;
  background-image: url("oko2.png");
  background-size:contain;                   
  background-repeat:   no-repeat;
  background-position: center center;
  box-shadow: 1px 1px 1px 1px rgba(35, 35, 35, 0.7);
}
.scanButtons{
  position: absolute;
  top: 80%;
  right: 10vw;
  display: flex;

}
.scanButtonsSpacer{
  width: 10vw;
}
.ItemConteiner{
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black
}
.ItemCode{
  flex: 1;
  padding: 10px;
  text-align: left;

}
.ItemInfo{
  flex: 2;
  text-align: center;
  border-left: 1px solid black;

}
.AllTickets{
  text-align: center;
padding-bottom: 10vh;
}
.NavMenu{
padding: 1px;
}
.NavMenuBtn{
  margin-right: 20px;
  border-radius: 20%;
  width: 10vw;
  height: 5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  }
.hamburger{
  transform: rotate(90deg);
}
.skaner{ 
 margin: 0 6vh;
  width: 80vw;
  height: 50vh;
}